body {
  background-color: lightgrey;
  text-align: center;
  padding: 1%;
  margin: 1%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-size: 140%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-size: 4rem;
  color: black;
  text-shadow: 2px 2px 1px red;
}

div {
  display: block;
  border-style: solid;
  border-radius: 0%;
  border-color: white;
}

button {
	background-color: white;
	border-radius: 3px;
	border:1px solid rgb(180, 0, 0);
	display:inline-block;
	cursor:pointer;
	color:black;
	font-family:Arial;
	font-size:24px;
	padding:12px 24px;
	text-decoration:none;
  text-shadow:0px 1px 0px #ff0000;
  box-shadow: 2px 2px 2px 0 rgba(193,1,1,1) ;
}
button:hover {
  background-color: rgb(255, 149, 149);
  color: white;
}
button:active {
	position:relative;
	top:1px;
}

input {
  display: inline-block;
  box-sizing: content-box;
  padding: 6px;
  border: 2px solid #440101;
  border-radius: 2px;
  font: normal normal bold 24px/normal Impact, Charcoal, sans-serif;
  color: rgba(0,0,0,1);
  text-overflow: clip;
  background: rgba(252,252,252,1);
  box-shadow: 2px 2px 2px 0 rgba(193,1,1,1) ;
  transition: all 200ms cubic-bezier(0.42, 0, 0.58, 1);
}

.Blog {

  box-sizing: content-box;
  padding: 1%;
  border: 2px solid;
  border-radius: 2px;
  font: normal 24px/1 Impact, Charcoal, sans-serif;
  color: rgba(0,0,0,1);
  text-align: center;
  text-overflow: ellipsis;
  background: #ffffff;
  box-shadow: 1px 1px 1px 0 rgba(0,0,0,0.3) ;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.2) ;
}

.votes {
  font-family:Arial;
  font-size:30px;
  text-shadow:0px 1px 0px #ff0000;
}